d: linear-gradient(90deg, #2c5cf7 0%, rgba(44, 92, 247, 0.8) 100%); }\n\n.custom-theme .color {\n  color: #2c5cf7; }\n\n.custom-theme .border-color {\n  border-color: #2c5cf7; }\n  .custom-theme .border-color::after {\n    border-color: #2c5cf7; }\n\n.test-theme .color {\n  color: #f00; }\n\n.test-theme .border-color {\n  border-color: #f00; }\n  .test-theme .border-color::after {\n    border-color: #f00; }\n\n/* \n * 多倍屏下边框兼容方案\n * 调用例子：\n * 1像数上边框 @include thinBorder(top);\n * 1像数红色上边框和左边框 @include thinBorder((top,left), red);\n * 1像数红色上边框和圆角100px @include thinBorder(top, red, 100px);\n*/\n.contract-item-wrapper {\n  width: 100%;\n  background: #fff;\n  border-radius: 6px; }\n  .contract-item-wrapper .title-con {\n    margin-left: 14px;\n    min-height: 50px;\n    padding: 15px 0;\n    font-size: 14px;\n    color: #000;\n    display: flex;\n    align-items: center;\n    box-sizing: border-box; }\n  .contract-item-wrapper .item-con {\n    margin-left: 14px;\n    min-height: 50px;\n    padding: 15px 0;\n    font-size: 14px;\n    color: #666666;\n    display: flex;\n    align-items: center;\n    box-sizing: border-box; }\n    .contract-item-wrapper .item-con .icon-box {\n      position: relative;\n      margin-left: 15px;\n      margin-right: 10px; }\n      .contract-item-wrapper .item-con .icon-box .super-app-iconfont {\n        font-size: 20px; }\n      .contract-item-wrapper .item-con .icon-box .light {\n        position: absolute;\n        top: 0;\n        left: 0;\n        color: #2c5cf7; }\n    .contract-item-wrapper .item-con .name {\n      flex: 1;\n      width: 0; }\n    .contract-item-wrapper .item-con .info-icon {\n      margin-right: 15px; }\n  .contract-item-wrapper .bottom-border {\n    border-bottom: 1px solid #ececec;\n    border-radius: 0 0 0 0; }\n    @media only screen and (-webkit-min-device-pixel-ratio: 2) {\n      .contract-item-wrapper .bottom-border {\n        position: relative;\n        border-bottom: none; }\n      .contract-item-wrapper .bottom-border:after {\n        content: '';\n        position: absolute;\n        top: 0;\n        left: 0;\n        display: block;\n        width: 200%;\n        height: 200%;\n        transform: scale(0.5);\n        box-sizing: border-box;\n        padding: 1px;\n        transform-origin: 0 0;\n        pointer-events: none;\n        border: 0 solid #ececec;\n        border-bottom-width: 1px;\n        border-radius: 0 0 0 0; } }\n    @media only screen and (-webkit-min-device-pixel-ratio: 3) {\n      .contract-item-wrapper .bottom-border:after {\n        border-radius: 0 0 0 0;\n        width: 300%;\n        height: 300%;\n        transform: scale(0.3333); } }\n","// 主题色\n$color-theme: #2c5cf7;\n$color-theme-light: #2c5cf7;\n// border颜色\n$color-border: #ebebeb;\n// CONTENT\n$color-body-bg: rgba(244, 244, 244, 1);\n$color-text: #202020;\n$color-text-light: #606060;\n$color-red: #eb544d;\n$color-blue: #0099ff;\n$color-purple: #a13eff;\n$color-grey: #a0a0a0;\n$color-green: #00cb59;\n$color-icon: #8a8a8a;\n$color-error: #ff3b30;\n\n@mixin theme($color-theme) {\n  .color {\n    color: $color-theme;\n  }\n  .border-color {\n    border-color: $color-theme;\n    &::after {\n      border-color: $color-theme;\n    }\n  }\n}\n\n.custom-theme {\n  @include theme('#2c5cf7');\n  .gradient-background {\n    background: linear-gradient(\n      90deg,\n      rgba($color-theme, 1) 0%,\n      rgba($color-theme, 0.8) 100%\n    );\n  }\n}\n\n.custom-theme {\n  @include theme(#2c5cf7);\n}\n\n.test-theme {\n  @include theme(#f00);\n}\n","/* \n * 多倍屏下边框兼容方案\n * 调用例子：\n * 1像数上边框 @include thinBorder(top);\n * 1像数红色上边框和左边框 @include thinBorder((top,left), red);\n * 1像数红色上边框和圆角100px @include thinBorder(top, red, 100px);\n*/\n@mixin thinBorder(\n  $directionMaps: bottom,\n  $color: #ccc,\n  $radius: (\n    0,\n    0,\n    0,\n    0,\n  ),\n  $position: after\n) {\n  // 是否只有一个方向\n  $isOnlyOneDir: string==type-of($directionMaps);\n\n  @if ($isOnlyOneDir) {\n    $directionMaps: ($directionMaps);\n  }\n\n  @each $directionMap in $directionMaps {\n    border-#{$directionMap}: 1px solid $color;\n  }\n\n  // 判断圆角是list还是number\n  @if (list==type-of($radius)) {\n    border-radius: nth($radius, 1) nth($radius, 2) nth($radius, 3) nth($radius, 4);\n  } @else {\n    border-radius: $radius;\n  }\n\n  @media only screen and (-webkit-min-device-pixel-ratio: 2) {\n    & {\n      position: relative;\n\n      // 删除1像素密度比下的边框\n      @each $directionMap in $directionMaps {\n        border-#{$directionMap}: none;\n      }\n    }\n\n    &:#{$position} {\n      content: '';\n      position: absolute;\n      top: 0;\n      left: 0;\n      display: block;\n      width: 200%;\n      height: 200%;\n      transform: scale(0.5);\n      box-sizing: border-box;\n      padding: 1px;\n      transform-origin: 0 0;\n      pointer-events: none;\n      border: 0 solid $color;\n\n      @each $directionMap in $directionMaps {\n        border-#{$directionMap}-width: 1px;\n      }\n\n      // 判断圆角是list还是number\n      @if (list==type-of($radius)) {\n        border-radius: nth($radius, 1) *\n          2\n          nth($radius, 2) *\n          2\n          nth($radius, 3) *\n          2\n          nth($radius, 4) *\n          2;\n      } @else {\n        border-radius: $radius * 2;\n      }\n    }\n  }\n\n  @media only screen and (-webkit-min-device-pixel-ratio: 3) {\n    &:#{$position} {\n      // 判断圆角是list还是number\n      @if (list==type-of($radius)) {\n        border-radius: nth($radius, 1) *\n          3\n          nth($radius, 2) *\n          3\n          nth($radius, 3) *\n          3\n          nth($radius, 4) *\n          3;\n      } @else {\n        border-radius: $radius * 3;\n      }\n\n      width: 300%;\n      height: 300%;\n      transform: scale(0.3333);\n    }\n  }\n}\n"]}]);
