<template>
  <div class="contract-item-wrapper" @click="$emit('click')">
    <div v-for="(item, index) in list" :key="index">
      <div
        class="item-con"
        :class="{ 'bottom-border': index !== list.length - 1 }"
        @click="toDetail(item)"
      >
        <div class="icon-box">
          <i class="super-app-iconfont super-app-iconcontract-apply"></i>
          <i
            class="super-app-iconfont super-app-iconcontract-apply-in light"
          ></i>
        </div>
        <div class="name" v-if="type == 'auth'">{{ item.name }}</div>
        <div class="name" v-else>{{ item.contract_keyword }}</div>
        <van-icon name="info-o" class="info-icon" v-if="showInfo" />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    type: {
      type: String,
      default: '',
    },
    list: {
      type: Array,
      default: () => [],
    },

    showInfo: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      // list: [
      //   {
      //     type: 'title',
      //     title: '项目名称',
      //   },
      //   {
      //     type: 'item',
      //     name: '门窗合同',
      //   },
      // ],
    };
  },
  methods: {
    toDetail(item) {
      if (this.type !== 'approve') return;

      this.$router.push({
        name: 'applyDetail',
        params: { applyId: item.apply_user_id, keywordId: item.id },
      });
    },
  },
};
</script>

