<template>
  <van-pull-refresh
    v-model="isLoading"
    @refresh="onRefresh"
    class="manage-list-wrapper"
  >
    <empty-page emptyMsg="暂无消息" :showEmpty="!list.length" />
    <van-list :finished="true" :immediate-check="false" class="list">
      <app-fold
        v-for="company in list"
        :key="company.org_id"
        :title="company.proj_name"
        class="mt10"
        :showTitleLine="true"
        :defaultFold="true"
      >
        <Item
          :list="company.contract_list"
          :showInfo="type == 'approve'"
          :type="type"
        ></Item>
      </app-fold>
    </van-list>
  </van-pull-refresh>
</template>

<script>
import AppFold from '@components/app-fold';
import Item from './contract-item';
import { PullRefresh } from 'vant';
import { getApplyContractList } from '@src/api/user-state';

const typeMap = {
  auth: { approvalState: 1 },
  approve: { approvalState: 0 },
};

export default {
  props: {
    type: {
      type: String,
      default: '',
    },
  },
  components: {
    [PullRefresh.name]: PullRefresh,
    AppFold,
    Item,
  },
  data() {
    return {
      isLoading: false,
      list: [],
    };
  },

  created() {
    this.init();
  },

  activated() {
    this.init();
  },

  methods: {
    init() {
      if (this.loading) {
        return false;
      }

      this.loading = false;
      this.list = [];
      this.load();
    },

    load() {
      if (this.loading) {
        return Promise.reject(false);
      }

      this.loading = true;

      return getApplyContractList(typeMap[this.type])
        .then(res => {
          this.list = res || [];
          this.loading = false;
          return Promise.resolve();
        })
        .catch(err => {
          console.log('获取失败', err);
          this.loading = false;
          return Promise.reject();
        });
    },

    onRefresh() {
      if (this.loading) {
        return false;
      }

      this.isLoading = true;
      this.load()
        .then(() => {
          this.isLoading = false;
        })
        .catch(() => {
          this.isLoading = false;
          console.log('this.isLoading  ===> ', this.isLoading);
        });
    },

    openFlow(item) {
      if (item.approval_state !== '1') {
        this.$router.push({
          path: '/user/apply-info',
          query: { applyId: item.apply_id, projectId: item.project_id },
        });
      }
      return false;
    },
  },
};
</script>

